import { Fragment } from "react"

export default function Contact(){
    return (
        <Fragment>
            <div>
                <div className="breadCrum">
                    <h4>
                        Contact
                    </h4>
                </div>
                <div className="right">
                    <h2>
                        Get in touch
                    </h2>
                    <h4 className="text-[#a5acc0]">
                        Want to get in touch? We'd love to hear from you. Here's how you can reach us.
                    </h4>
                </div>
            </div>
        </Fragment>
    )
}